.article
{
    display: flex;
    flex-wrap: wrap;
}

p
{
    margin-top: 0;
}

.subtitle
{
    width: 100%;
    text-decoration: underline;
}

.paragraph_50
{
    padding-right: 1em;
    width: calc(50% - 1em);
}

.padding1em{
    padding: 0 1em;
}

.paragraph_50.padding1em
{
    width: calc(50% - 2em);
}

.paragraph_100
{
    width: 100%;
}

img,.margincontent > *
{
    display: block;
    margin: auto;
}

.maxcontent
{
    width: max-content;
}

.list
{
    padding-left: 0;
    list-style-position: inside;
}

@media (max-width: 768px) {
    .paragraph_50, .paragraph_50.padding1em{
        width: 100%;
    }

    .padding1em{
        padding: 0;
    }
  }